import {  SET_PRODUCT_LIST,SET_FETCH_SERVICE } from "./constant"

export const productData = (data = [], action) => {
    switch (action.type) {
            case SET_PRODUCT_LIST:
                console.warn("PRODUCT_LIST condition ", action)
                return [...action.data.data.rows]
        default:
            return data
    }
}

export const crudData = (data = [], action) => {
    switch (action.type) {
            case SET_FETCH_SERVICE:
                return [...action.data]
        default:
            return data
    }
}